<template>
  <div v-if="currentList" class="edit-form">
    <h4>List</h4>
    <form>
      <div class="form-group">
        <label for="title">Title</label>
        <input type="text" class="form-control" id="title"
          v-model="currentList.title"
        />
      </div>
      <div class="form-group">
        <label for="description">Description</label>
        <input type="text" class="form-control" id="description"
          v-model="currentList.description"
        />
      </div>

      <div class="form-group">
        <label><strong>Status:</strong></label>
        {{ currentList.published ? "Done" : "Pending" }}
      </div>
    </form>

    <button class="badge badge-primary mr-2"
      v-if="currentList.published"
      @click="updatePublished(false)"
    >
      UnDone
    </button>
    <button v-else class="badge badge-primary mr-2"
      @click="updatePublished(true)"
    >
      Done
    </button>

    <button class="badge badge-danger mr-2"
      @click="deleteList"
    >
      Delete
    </button>

    <button type="submit" class="badge badge-success"
      @click="updateList"
    >
      Update
    </button>
    <p>{{ message }}</p>
  </div>

  <div v-else>
    <br />
    <p>Please click on a List...</p>
  </div>
</template>

<script>
import ListDataService from "../services/ListDataService";

export default {
  name: "list",
  data() {
    return {
      currentList: null,
      message: ''
    };
  },
  methods: {
    getList(id) {
      ListDataService.get(id)
        .then(response => {
          this.currentList = response.data;
          console.log(response.data);
        })
        .catch(e => {
          console.log(e);
        });
    },

    updatePublished(status) {
      var data = {
        id: this.currentList.id,
        title: this.currentList.title,
        description: this.currentList.description,
        published: status
      };

      ListDataService.update(this.currentList.id, data)
        .then(response => {
          console.log(response.data);
          this.currentList.published = status;
          this.message = 'The status was updated successfully!';
        })
        .catch(e => {
          console.log(e);
        });
    },

    updateList() {
      ListDataService.update(this.currentList.id, this.currentList)
        .then(response => {
          console.log(response.data);
          this.message = 'The list was updated successfully!';
        })
        .catch(e => {
          console.log(e);
        });
    },

    deleteList() {
      ListDataService.delete(this.currentList.id)
        .then(response => {
          console.log(response.data);
          this.$router.push({ name: "lists" });
        })
        .catch(e => {
          console.log(e);
        });
    }
  },
  mounted() {
    this.message = '';
    this.getList(this.$route.params.id);
  }
};
</script>

<style>
.edit-form {
  max-width: 300px;
  margin: auto;
}
</style>
